<template>
  <div class="form">
    
    <input class="form__input" type="text" placeholder="메세지를 입력하세요."  v-model.trim="msg" @keyup.enter="submitMessage"/>

    <div @click="submitMessage" class="form__submit">
      <svg  width="30" height="30" viewBox="0 0 68 68" fill="#CCCCCC" xmlns="http://www.w3.org/2000/svg">
        <g clip-path="url(#clip0_26_10)">
          <path fill-rule="evenodd" clip-rule="evenodd"
            d="M48.0833 19.799C48.619 20.3347 48.806 21.127 48.5665 21.8457L35.8385 60.0294C35.5946 60.7614 34.9513 61.2877 34.1855 61.382C33.4198 61.4763 32.6681 61.1217 32.2539 60.4707L22.593 45.2893L7.41158 35.6285C6.76065 35.2142 6.40604 34.4625 6.50031 33.6968C6.59458 32.931 7.12092 32.2878 7.85287 32.0438L46.0366 19.3159C46.7553 19.0763 47.5476 19.2633 48.0833 19.799ZM26.5903 44.1204L33.3726 54.7782L42.0926 28.6181L26.5903 44.1204ZM39.2642 25.7897L23.7619 41.292L13.1041 34.5097L39.2642 25.7897Z"
            fill=""/>
        </g>
        <defs>
          <clipPath id="clip0_26_10">
            <rect width="48" height="48" fill="white" transform="translate(33.9412) rotate(45)" />
          </clipPath>
        </defs>
      </svg>
    </div>

  </div>
</template>

<script>
export default {
data() {
  return {
      msg: "",
      post_id: 0,
    }
  },
  methods: {
    submitMessage() {
      if (this.msg) {
        this.$emit("submitMessage");

        const post_id = this.$store.state.postIdList[this.$store.state.roomIndex];

        const date = new Date();

        const year = date.getFullYear();
        const month = ('0' + (date.getMonth() + 1)).slice(-2);
        const day = ('0' + date.getDate()).slice(-2);
        const dateString = year + '-' + month  + '-' + day;

        const hours = ('0' + date.getHours()).slice(-2); 
        const minutes = ('0' + date.getMinutes()).slice(-2);
        const seconds = ('0' + date.getSeconds()).slice(-2); 
        const timeString = hours + ':' + minutes  + ':' + seconds;

        const chatData = {
          from_name: this.$store.state.member.nick,
          time: dateString + " " + timeString,
          message: this.msg,
          post_id: post_id
        };
        this.$store.dispatch('POST_CHAT_DATA', chatData);

        this.$store.dispatch('FIND_CHAT_LOGS', post_id);

        this.msg = "";
      }
    },
  }
}
</script>

<style scoped>
.form {
  padding: 1.3rem;
  background: #ffffff;
  border-radius: 0px 0px 24px 0px;
  box-shadow: 0px -5px 30px rgba(0, 0, 0, 0.05);
  display: flex;
  justify-content: space-between;
}
.form__input {
  width: calc(100% - 60px);
  padding: 0.5rem;
  border: none;
  font-size: 16px;
}
.form__input:focus {
  outline: none;
}
.form__submit {
  display: flex;
  align-items: center;
  cursor: pointer;
}
svg {
  transition: 0.3s;
}
svg:hover {
  fill: #999999;
}
</style>
